/* Navbar.css */

/* Navbar principale */
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #2b2b2b;
    color: #fff; /* Couleur de texte par défaut */
    padding: 20px 40px;
    z-index: 1000;
}

/* Liens de la navbar */
.navbar-links {
    list-style-type: none;
    display: flex;
    justify-content: center; /* Aligne les éléments au centre */
    align-items: center;
    margin: 0;
    padding: 0;
    flex: 1; /* Permet à la liste de prendre tout l'espace restant */
}

.navbar-links li {
    padding: 0 10px;
}

.navbar-links a {
    color: #fff; /* Couleur de texte par défaut */
    text-decoration: none;
    font-size: 18px;
    position: relative; /* Pour le pseudo-élément */
}

/* Pseudo-élément pour créer l'effet de survol */
.navbar-links a::before {
    content: ""; /* Contenu vide */
    position: absolute;
    bottom: -2px; /* Position par rapport au bas du lien */
    left: 0;
    width: 100%;
    height: 2px; /* Épaisseur de la ligne */
    background-color: transparent; /* Couleur de fond par défaut */
    transition: width 0.3s ease; /* Animation de transition pour la largeur */
}

/* Effet de survol */
.navbar-links a:hover::before {
    width: 100%; /* Pleine largeur */
    background-color: royalblue; /* Couleur de la ligne au survol */
}

/* Burger Menu */
.burger {
    display: none;
    cursor: pointer;
}

.line {
    width: 25px;
    height: 3px;
    background-color: #fff;
    margin: 5px;
    transition: all 0.3s ease;
}

.burger.open .line1 {
    transform: rotate(-45deg) translate(-5px, 6px);
}

.burger.open .line2 {
    opacity: 0;
}

.burger.open .line3 {
    transform: rotate(45deg) translate(-5px, -6px);
}

/* Media query pour le burger menu sur les petits écrans */
@media screen and (max-width: 768px) {
    .navbar-links {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 60px;
        right: 0;
        width: 100%;
        background-color: #333;
        z-index: 1000;
    }

    .navbar-links.active {
        display: flex;
    }

    .burger {
        display: block;
    }
}
