/* Stage.css */

body {
    background-color: #282c34;
    margin: 0; /* Pour enlever les marges par défaut du body */
    color: #fff; /* Pour rendre le texte visible sur un fond sombre */
    font-family: 'Arial', sans-serif;
}

/* Style pour les navigateurs WebKit */
::-webkit-scrollbar {
    width: 12px; /* Largeur de la barre de défilement */
}

::-webkit-scrollbar-track {
    background: #282c34; /* Couleur de l'arrière-plan de la piste de défilement */
}

::-webkit-scrollbar-thumb {
    background-color: #007BFF; /* Couleur de la barre de défilement */
    border-radius: 10px; /* Arrondir les bords */
    border: 3px solid #282c34; /* Ajouter une bordure pour espacer la barre de défilement de la piste */
}

::-webkit-scrollbar-thumb:hover {
    background-color: #0056b3; /* Couleur de la barre de défilement au survol */
}

/* Style pour Firefox */
* {
    scrollbar-width: thin; /* Largeur fine de la barre de défilement */
    scrollbar-color: #007BFF #282c34; /* Couleur de la barre de défilement et de la piste */
}

.stage-container {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    padding: 20px; /* Ajouter du padding pour éviter que le texte touche les bords */
    background-color: #282c34; /* Ajouter un fond pour le conteneur */
}

.stage-buttons {
    margin-bottom: 20px;
}

.stage-btn {
    background-color: #007BFF;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin: 0 10px;
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease;
}

.stage-btn:hover {
    background-color: #0056b3;
}

.stage-btn.active {
    background-color: #0056b3;
}

.stage-table {
    display: none;
}

.stage-table.active {
    display: block;
}

.stage-table table {
    width: 100%;
    border-collapse: collapse;
    border: 1px solid #ddd;
    margin-top: 20px;
}

.stage-table h2 {
    margin-top: 40px;
}

.stage-table thead {
    background-color: #f2f2f2;
}

.stage-table th, .stage-table td {
    padding: 8px;
    text-align: center;
    border-bottom: 1px solid #ddd;
}

.stage-table th {
    background-color: #333;
    color: #fff;
}
